import config from '../config'
import Provider from '../models/Provider';
import { EventOdd, Result } from '../models/EventOdd';
import { OpenBet } from '../models/BetOrder';
import _ from 'lodash'
import moment from 'moment'
import axios from 'axios'
import xml2js from'xml2js-es6-promise'

const fetch_pickMon_eventOdds = async () => {
 	try{
 		const pickMonApi = await Provider.findOne({ provider: 'pickMon', activate: true })

 		if(pickMonApi){
		 	const response = await axios.get(`${pickMonApi.apiLink}uid=${config.pickMon_UID}&key=${config.pickMon_Key}&sports=${pickMonApi.option.sportLeagues.join(',')}&graded=0&full_call=${pickMonApi.option.eventFullCall}`)
		 	if(!_.isEmpty(response.data)){
		 		const pickMonData = await xml2js(response.data, {explicitArray: false})
			 	if(!_.isEmpty(pickMonData.lines) && !_.isEmpty(pickMonData.lines.game) && _.isFunction(pickMonData.lines.game.map)){
					await Promise.all(pickMonData.lines.game.map(async event => {
				 		if( event.header && !event.line.score.winner && moment().isBefore(moment(event.line.wagercutoff).subtract(1, 'h').format()) && event.void === '0' && !event.team1.name.includes('/')){

							let newEventOdd = {
								source: {
									provider: `pickMon`,
									bookmaker: `default`,
									id: event.id,
									lastUpdated: moment(event.last_update).subtract(3, 'h')
								},
								sport: event.sporttype,
								matchTime: moment(event.gamedate).subtract(3, 'h'),
								team: {
									home: event.team2.name,
									homeROT: event.team2.rotnum,
									away: event.team1.name,
									awayROT: event.team1.rotnum
								},
								details: event.header || '',
								odds: {
									moneyLineHome: Number(event.line.money.team2) || 0,
									moneyLineAway: Number(event.line.money.team1) || 0,
									pointSpreadHome: Number(event.line.spread.points) || 0,
									pointSpreadHomeLine: Number(event.line.spread.team2) || 0,
									pointSpreadAwayLine: Number(event.line.spread.team1) || 0,
									totalNumber: Number(event.line.total.points) || 0,
									overLine: Number(event.line.total.over) || 0,
									underLine: Number(event.line.total.under) || 0,
									drawLine: Number(event.line.money.draw) || 0
								},
								updatedAt: moment(),
							}

							const checkOddPoints = _.pick(newEventOdd.odds, [
								'moneyLineHome', 
								'moneyLineAway', 
								'pointSpreadAway',
								'pointSpreadHome',
								'pointSpreadHomeLine',
								'pointSpreadAwayLine',
								'totalNumber',
								'overLine',
								'underLine',
								'drawLine'])

							const allZero = Object.values(checkOddPoints).every(val => Number(val) === 0)

							if(!allZero){

								if(Number(event.line.spread.points) > 0){
									newEventOdd.odds.pointSpreadAway = -Number(event.line.spread.points)
								}else{
									newEventOdd.odds.pointSpreadAway = Math.abs(Number(event.line.spread.points))
								}

								if(event.sporttype === 'Baseball'){
									newEventOdd.team.homePitcher = event.team2.pitcher || 'Action'
									newEventOdd.team.awayPitcher = event.team1.pitcher || 'Action'
								}

								if(event.sporttype !== 'Soccer'){
									newEventOdd.league = event.sportsubtype
									newEventOdd.region = ''
								}

								if(event.sporttype === 'Soccer'){
									newEventOdd.league = ''
									newEventOdd.region = event.sportsubtype
								}

								switch(event.line.perioddesc){
									case 'Game':
										newEventOdd.oddType = 'Game';
										break;
									case '1st Half':
										newEventOdd.oddType = 'First Half';
										break;
									case '2nd Half':
										newEventOdd.oddType = 'Second Half';
										break;
									case '1st Quarter':
										newEventOdd.oddType = 'First Quarter';
										break;
									case '2nd Quarter':
										newEventOdd.oddType = 'Second Quarter';
										break;
									case '3rd Quarter':
										newEventOdd.oddType = 'Third Quarter';
										break;
									case '4rd Quarter':
										newEventOdd.oddType = 'Fourth Quarter';
										break;
									case '1st 5 Innings':
										newEventOdd.oddType = 'First Five Innings';
										break;
									default:
										console.log('new event !!!!!!!!!!!!!!!! ' + event.line.perioddesc)
										return;
								}

								// if(newEventOdd.oddType === 'Game'){
								// 	newEventOdd.cutOffTime = moment(event.gamedate).subtract(3, 'h').subtract(1, 's')
								// 	newEventOdd.expireAt = moment(event.gamedate).subtract(3, 'h').subtract(1, 's')
								if(newEventOdd.oddType === 'Second Half'){
									if(moment(event.gamedate).add(1, 'h').add(55, 'm').isAfter(event.line.wagercutoff)){
										newEventOdd.cutOffTime = moment(event.line.wagercutoff).subtract(3, 'h').subtract(1, 's')
										newEventOdd.expireAt = moment(event.line.wagercutoff).subtract(3, 'h')
									}else{
										newEventOdd.cutOffTime = moment(event.gamedate).add(1, 'h').add(55, 'm').subtract(3, 'h').subtract(1, 's')
										newEventOdd.expireAt = moment(event.gamedate).add(1, 'h').add(55, 'm').subtract(3, 'h')
									}
								}else{
									newEventOdd.cutOffTime = moment(event.gamedate).subtract(3, 'h').subtract(1, 's')
									newEventOdd.expireAt = moment(event.gamedate).subtract(3, 'h')

								}

								newEventOdd.uniqueId = `${newEventOdd.team.homeROT}_${newEventOdd.team.awayROT}_${newEventOdd.sport.replace(/\s/g,'').toUpperCase()}_${newEventOdd.oddType.replace(/\s/g,'').toUpperCase()}_${moment(newEventOdd.matchTime).format('MMDDYYYY')}`

					 			const existedEventOdd = await EventOdd.findOne({ uniqueId: newEventOdd.uniqueId })
								if(_.isEmpty(existedEventOdd)){

									// if(newEventOdd.expireAt.isBefore(moment())){
									// 	if(newEventOdd.oddType === 'Second Half') {
									// 		console.log(event.id)
									// 	}
										await new EventOdd(newEventOdd).save()
										console.log(`saved ${newEventOdd.uniqueId}`)
									//}

								}else if(!moment(existedEventOdd.source.lastUpdated).isSame(moment(newEventOdd.source.lastUpdated).format())){
									await EventOdd.findOneAndUpdate({ uniqueId: newEventOdd.uniqueId }, { $set: newEventOdd })
									console.log(`updated ${newEventOdd.uniqueId}`)
								}else{
									console.log(`${newEventOdd.uniqueId} is up to date`)
								}
							}
				 		}

						return null
					}))		 		
			 	}else{
		 			console.log('pickmon has no update')
		 		}
		 	}
 		}
 	}catch(err){
 		throw err
 	}
}

export default fetch_pickMon_eventOdds